<template>
  <div id="app" class="web-camera-container">
    <input v-model="accessKeyId" placeholder="accessKeyId">
    <input v-model="secretAccessKey" placeholder="secretAccessKey">
    <div class="camera-button">
        <button type="button" class="button is-rounded" @click="update_aws">
          <span>CreateAWS</span>
      </button>
    </div>
    <div class="camera-button">
        <button type="button" class="button is-rounded" :class="{ 'is-primary' : !isCameraOpen, 'is-danger' : isCameraOpen}" @click="toggleCamera">
          <span v-if="!isCameraOpen">Open Camera</span>
          <span v-else>Close Camera</span>
      </button>
    </div>
    
    <div v-show="isCameraOpen && isLoading" class="camera-loading">
      <ul class="loader-circle">
        <li></li>
        <li></li>
        <li></li>
      </ul>
    </div>
    
   
    
    <div class="err">{{this.error}}</div>
    <div class="rs">{{ this.rs }}</div>

    <div v-if="isCameraOpen" v-show="!isLoading" class="camera-box" :class="{ 'flash' : isShotPhoto }">
      
      <div class="camera-shutter" :class="{'flash' : isShotPhoto}"></div>
        
      <video  ref="camera" :width="600" :height="400" autoplay></video>
      
      <canvas  id="photoTaken" ref="canvas" :width="450" :height="337.5"></canvas>
      <!-- <canvas  id="photoTaken2" ref="canvas2" :width="450" :height="337.5"></canvas> -->

    </div>
    
    <div v-if="isCameraOpen && !isLoading" class="camera-shoot">
      <button type="button" class="button" @click="takePhoto">
        <img src="https://img.icons8.com/material-outlined/50/000000/camera--v2.png">
      </button>
    </div>
    <!-- <div v-if="isPhotoTaken && isCameraOpen" class="camera-download">
      <a id="downloadPhoto" download="my-photo.jpg" class="button" role="button" @click="downloadImage">
        Download
      </a>
    </div> -->
  </div>
</template>

<script>
import AWS from 'aws-sdk';
AWS.config.update({region:'us-east-2',accessKeyId:'',secretAccessKey:''});
// eslint-disable-next-line no-unused-vars
const rekognition = new AWS.Rekognition()

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  created() {
    
  },
  data() {
    return {
      secretAccessKey: '',
      accessKeyId: '',
      isCameraOpen: false,
      isPhotoTaken: false,
      isShotPhoto: false,
      isLoading: false,
      sourceImg: null,
      targetImg: [],
      tempcontext: null,
      tempcanvas: null,
      error: '',
      rs: ''
    }
  },
  methods: {
    update_aws() {
      rekognition.config.update({region:'us-east-2',accessKeyId: this.accessKeyId,secretAccessKey: this.secretAccessKey});
      this.tempcanvas = document.createElement('canvas');
      this.tempcanvas.width = 450;
      this.tempcanvas.height = 337.5;
      this.tempcontext = this.tempcanvas.getContext('2d');
      setInterval(()=>{
        if(this.isCameraOpen && this.isPhotoTaken && this.sourceImg && this.tempcontext) {
          this.tempcontext.drawImage(this.$refs.camera, 0, 0, 450, 337.5);

          var data = this.tempcanvas.toDataURL('image/jpeg');
          var base64Image = data.replace(/^data:image\/(png|jpeg|jpg);base64,/, '');
          this.targetImg = this.getBinary(base64Image);

          this.compare()
        }
      }, 7000)
    },
    toggleCamera() {
      if(this.isCameraOpen) {
        this.isCameraOpen = false;
        this.isPhotoTaken = false;
        this.isShotPhoto = false;
        this.stopCameraStream();
      } else {
        this.isCameraOpen = true;
        this.createCameraElement();
      }
    },
    createCameraElement() {
      this.isLoading = true;
      
      const constraints = (window.constraints = {
				audio: false,
				video: true
			});
			navigator.mediaDevices
				.getUserMedia(constraints)
				.then(stream => {
          this.isLoading = false;
					this.$refs.camera.srcObject = stream;
				})
				// eslint-disable-next-line no-unused-vars
				.catch(error => {
          this.isLoading = false;
					alert("May the browser didn't support or there is some errors.");
				});
    },
    stopCameraStream() {
      let tracks = this.$refs.camera.srcObject.getTracks();

			tracks.forEach(track => {
				track.stop();
			});
    },
    
    takePhoto() {
      if(!this.isPhotoTaken) {
        this.isShotPhoto = true;

        const FLASH_TIMEOUT = 50;

        setTimeout(() => {
          this.isShotPhoto = false;
        }, FLASH_TIMEOUT);
      }
      
      this.isPhotoTaken = !this.isPhotoTaken;
      
      const context = this.$refs.canvas.getContext('2d');
      context.drawImage(this.$refs.camera, 0, 0, 450, 337.5);

      var data = this.$refs.canvas.toDataURL('image/jpeg');
      var base64Image = data.replace(/^data:image\/(png|jpeg|jpg);base64,/, '');
      this.sourceImg = this.getBinary(base64Image);
     
    },
    getBinary(base64Image) {
      var binaryImg = atob(base64Image);
      var length = binaryImg.length;
      var ab = new ArrayBuffer(length);
      var ua = new Uint8Array(ab);
      for (var i = 0; i < length; i++) {
        ua[i] = binaryImg.charCodeAt(i);
      }

      return ab;
    },
    compare() {
      // let x = this.sourceImg.replace("data:image/jpeg;base64,", "")
      // let y = this.targetImg.replace("data:image/jpeg;base64,", "")
      console.log("compare",  this.sourceImg)
      this.error = "comparing"
      rekognition.compareFaces({
        SimilarityThreshold: 70,
        SourceImage: {Bytes: this.sourceImg},
        TargetImage: {Bytes: this.targetImg},
      }, (err, data)=> {
        if(err) {
          this.error = "khong tim thay mat hoac loi gif do"
          this.rs = ''
        }
        if(data) {
          console.log("data", data)
          this.error = ''
          if(data.FaceMatches.length > 0) {
            this.rs = `Matched - Similarity: ${data.FaceMatches[0].Similarity} \n Confidence: ${data.FaceMatches[0].Face.Confidence}`

          } else if(data.UnmatchedFaces.length > 0) {
            this.error = `Unmatched - Confidence: ${data.UnmatchedFaces[0].Confidence}`
          }
        }
      })
    }
  }
}
</script>

